html, body, #root, .App {
  height: 100%;
  width: 100%;
}

.App {
  background-color: #282c34;
  text-align: center;
  height: inherit;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.home {
  padding-top: 10vh;
}
.home p{
  font-size: 1.5em;
  color: #fff;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 10vh;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  padding-left: 5vh;
  justify-content: flex-start;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.loader{
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.header-right{
  display: flex;
  flex: 1;
  justify-content: flex-start;
  padding-left: 5vh;
}

.link{
  text-decoration: none;
  color:white;
}
.flex{
  display: flex;
  flex: 1;
}
.flex-center{
  justify-content: center;
}
.accordion{
  width: 50vh;
  margin-top: 5vh;
}
.flatpickr-input{
  max-height: 40px;
}
.flex-row-start{
  flex: 1;
  flex-direction: row;
  align-items: flex-start;
  display: flex;
}
.flatpickr-input{
  width: 250px;
  height: 40px;
  padding: 15px;
}
